div {
    border: 0px solid white;
  
  }
  
  .header_container {
    background-color: white;
    transition: background-color 0.9s;
    width: 100%;
    position: absolute;
    z-index: 1;
    display: block;
  }
  .header_container > .mall-plus {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    border-bottom: 1px solid #ebebeb;
  }
  .header_container > .mall-plus > .header-plus {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    width: 1200px;
    height: 36px;
    box-sizing: border-box;
    margin-left: -30px;
  }
  .header-plus ul {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
  }
  .header-plus ul:nth-child(2) {
    margin-left: -10px;
  }
  ul {
    padding: 0;
    margin: 0;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 2px;
  }
  
  .header-plus ul li:first-child {
    padding-left: 0;
  }
  .header-plus ul li:nth-child(odd) {
    padding: 0 14px;
  }
  .header-plus ul li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 36px;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  ul li {
    list-style: none;
  }
  .header-plus ul li a {
    color: #666;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    cursor: pointer;
    height: 40px;
    font-size: 13px;
    text-decoration: none;
  }
  .header-plus ul li a:hover {
    color: #ffca28;

  }.logoutButton:hover{
    color: #ffca28;
  }
  .header-plus ul li > span {
    display: block;
    height: 34px;
    color: #b2b2b2;
    line-height: 34px;
  }
  .lang-en {
    font-family: OpenSans-Regular, "PingFang SC", "Microsoft YaHei", sans-serif,
      Arial;
  }
  .header_container .wrapper {
    height: 60px;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;
    position: relative;
    width: 1200px;
    margin: 0 auto;
  }
  .header_container .wrapper .nav {
    display: block;
  }
  .header_container .wrapper .nav .nav-menu {
    width: 1060px;
    -webkit-box-pack: center;
    display: flex;
    justify-content: center;
  }
  .header_container .wrapper .nav ul {
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 25px;
  }
  .header_container .wrapper .nav ul.nav-menu > li {
    padding: 0 30px;
    height: 40px;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    cursor: pointer;
    list-style: none;
  }
  .header_container .nav ul.nav-menu > li > a {
    font-size: 15px;
    line-height: 44px;
    display: block;
    color: #000;
    white-space: nowrap;
    text-decoration: none;
  }
  .header_container .nav ul.nav-menu > li:first-child > div a {
    font-size: 20px;
    background-color: #ffca28;
    text-decoration: none;
    color: black;
    font-weight: 500;
    line-height: 20px;
    position: relative;
    letter-spacing: 0.1px;
    padding: 0 5px;
  }
  .navigation_bar {
    position: absolute;
    top: 60px;
    right: 60px;
    left: -168px;
    background: white;
    width: 1535px;
    box-sizing: 0 5px 25px rgba(0, 0, 0, 0.1);
    transition: 0.5s;
    color: black;
    visibility: hidden;
    opacity: 0;
    box-shadow: 1px 2px 6px 1px rgb(0 0 0 / 15%);
    border: 0px solid #dddfe5;
    height: auto;
  }
  .wrapper .nav .nav-menu .false .tagHeader:hover {
    color: #ffca28;

  }
  .navigation_bar_active {
    position: absolute;
    top: 60px;
    right: 60px;
    left: -168px;
    background: white;
    width: 1535px;
    box-sizing: 0 5px 25px rgba(0, 0, 0, 0.1);
    transition: 0.5s;
    color: black;
    visibility: visible;
    opacity: 1;
    box-shadow: 1px 2px 6px 1px rgb(0 0 0 / 15%);
    border: 0px solid #dddfe5;
    height: auto;
    padding-bottom: 20px;
  }
  
  .navigation_bar_container {
    width: 100%;
    margin: auto;
    margin-top: 1%;
  }
  .navigation_flex_box {
    display: flex;
    width: 85%;
    margin: auto;
  }
  .navigation_flex_box > div:nth-child(1) {
    flex-basis: 15%;
    margin: auto;
    height: auto;
    margin-left: 2%;
  }
  .navigation_flex_box > div:nth-child(2) {
    flex-basis: 82%;
    margin: 0 auto;
    height: auto;
    padding: 0 auto;
    margin-top: 15px;
  }
  .flex_child_box > ul {
    display: block;
    margin-top: 10px;
  }
  .flex_child_box > ul > li {
    padding: 5px 0px;
    margin: 0 35px 5px 0;
    font-weight: normal;
    text-align: left;
  }
  .icon-arrow-down {
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: top center;
  }
  .header .icon {
    width: 30px;
    height: 30px;
  }
  .flex_child_box_des .des_outer {
    display: flex;
    margin: auto;
    padding: auto;
  }
  .flex_child_box_des .des_outer > div {
    flex-basis: 30%;
    height: 100px;
    display: flex;
  }
  .flex_child_box_des .des_outer div > div:nth-child(1) {
    flex-basis: 40%;
    margin: auto;
  }
  .flex_child_box_des .des_outer div > div:nth-child(2) {
    flex-basis: 60%;
    margin: auto;
  }
  
  .details_div_container {
    display: block;
    margin-left: -6px;
  }
  .details_div_container > p {
    margin: 0;
    text-align: left;
    margin-left: -5px;
    padding: 1px 0;
  }
  .des_outer .image_div {
    width: 63%;
    margin: auto;
  }
  .des_outer .image_div > img {
    width: 100%;
    height: auto;
  }
  .new_p {
    color: #2eb4cb;
    font-weight: 500;
  }
  .price_p {
    color: #dd0b25;
    font-weight: 600;
  }
