  .grid_box_outer {
    width: 80%;
    margin: auto;
    margin-top: 2%;
    margin: 4% auto 1%;
  }
  .grid_box_outer > .grid_box_inner {
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-area: auto auto auto;
    grid-gap: 2%;
    grid-row-gap: 4%;
  }
  .grid_box_inner > div {
    display: flex;
    margin-bottom: 5%;
  }
  .grid_box_inner > div > div:nth-child(1) {
    flex-basis: 40%;
    margin: auto;
  }
  .grid_box_inner > div > div:nth-child(2) {
    flex-basis: 60%;
    margin: auto;
  }
  
  .grid_box_inner > div > div .image_div {
    width: 73%;
    margin: auto;
  }
  .grid_box_inner > div > div .image_div > img {
    width: 100%;
    height: auto;
  }
  
  /*search icon*/
  a {
