  }
  .nav-right {
    position: relative;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: end;
    justify-content: flex-end;
  }
  
  .nav-right .nav-search {
    display: block;
    right: 70px;
    top: 5px;
    cursor: pointer;
  }
  .nav-search .icon {
    width: 30px;
    height: 30px;
    display: block;
  }
  .icon-search {
    background-image: url(data:image/svg+xml;base64,);
